.block-compare-model .block__title {
  margin-bottom: 42px;
}

.block-compare-model .block__title h2 {
  line-height: 40px;
}

.block-compare-model .block__content {
  font-size: 15px;
}

.block-compare-model .block__content p {
  font-weight: 500;
}

.block-compare-model .block__video {
  margin: 30px 0 45px;
}

.block-compare-model .block__links {
  margin: 45px 0 0;
}

@media only screen and (max-width: 1023px) {
  .block-compare-model .block__title {
    margin-bottom: 30px;
  }

  .block-compare-model .block__content p {
    margin: 20px 0;
  }

  .block-compare-model .block__video {
    margin: 20px 0;
  }

  .block-compare-model .block__links {
    margin: 20px 0 0;
  }
}